import { Icon, LatLngExpression } from "leaflet";
import "leaflet/dist/leaflet.css";
import { MapContainer, Marker, Polyline, TileLayer } from "react-leaflet";
import line10 from "./routes/line10";
import line1 from "./routes/line1";
import line9 from "./routes/line9";
import line23 from "./routes/line2-3";
import line4 from "./routes/line4";
import line5 from "./routes/line5";
import line6 from "./routes/line6";
import line7 from "./routes/line7";
import line8 from "./routes/line8";

import icon from "./icons/icon.png";
import { useCallback, useEffect, useMemo, useState } from "react";
import { Helmet } from "react-helmet-async";

const customIcon = new Icon({
  iconUrl: icon,
  className: "custom-icon",
  iconSize: [15, 15],
});

const lines = [line1, line23, line4, line5, line6, line7, line8, line9, line10];
const colors = [
  "red",
  "blue",
  "green",
  "orange",
  "purple",
  "black",
  "magenta",
  "teal",
  "brown",
];

const options = [
  { value: 0, label: "Line 1" },
  { value: 1, label: "Line 2-3" },
  { value: 2, label: "Line 4" },
  { value: 3, label: "Line 5" },
  { value: 4, label: "Line 6" },
  { value: 5, label: "Line 7" },
  { value: 6, label: "Line 8" },
  { value: 7, label: "Line 9" },
  { value: 8, label: "Line 10" },
];

function App() {
  const [selectedLine, setSelectedLine] = useState<number | undefined>();
  const [isTextHidden, setIsTextHidden] = useState<boolean>(false);
  setTimeout(() => setIsTextHidden(true), 5000)

  const getCurve = useCallback(
    (
      markers: { marker: number[]; offsetx?: number; offsety?: number }[],
      isSelected?: boolean,
      color?: string,
      index?: number,
      hasSelectedLine?: boolean
    ) => {
      var randomColor = Math.floor(Math.random() * 16777215).toString(16);
      let oldMarker: number[] = [];
      return markers.flatMap((x, i) => {
        const pointXWithOffset = x.offsetx
          ? x.marker[0] + x.offsetx
          : x.marker[0];
        const pointYWithOffset = x.offsety
          ? x.marker[1] + x.offsety
          : x.marker[1];
        const newMarker = [pointXWithOffset, pointYWithOffset];
        const firstPoint = oldMarker;
        oldMarker = newMarker;
        if (i === 0) {
          return isSelected ? (
            <Marker
              key={randomColor + i}
              position={oldMarker as LatLngExpression}
              icon={customIcon}
            ></Marker>
          ) : (
            []
          );
        } else {
          return (
            <div key={randomColor + i}>
              {isSelected && (
                <Marker
                  position={oldMarker as LatLngExpression}
                  icon={customIcon}
                ></Marker>
              )}
              <Polyline
                className={isSelected ? "upper-line" : undefined}
                eventHandlers={{ click: () => setSelectedLine(index) }}
                positions={[
                  firstPoint as LatLngExpression,
                  [pointXWithOffset, pointYWithOffset] as LatLngExpression,
                ]}
                color={color ?? "#" + randomColor}
                opacity={isSelected ? 1 : hasSelectedLine ? 0.3 : 0.4}
                weight={isSelected ? 6 : 5}
              />
            </div>
          );
        }
      });
    },
    [setSelectedLine]
  );

  const lineCurves = useMemo(() => {
    return lines.map((x, i) =>
      getCurve(x, selectedLine === i, colors[i], i, !!selectedLine)
    );
  }, [getCurve, selectedLine]);

  return (
    <>
      <Helmet>
        <title>Explore Podgorica's Local Bus Routes</title>
        <meta
          name="description"
          content="Plan your journey and navigate Podgorica's streets with ease using our comprehensive guide to the city's local bus routes. Find detailed information about bus lines and stops, ensuring a smooth and efficient transportation experience."
        ></meta>
        <meta
          name="keywords"
          content="Podgorica bus routes, local buses, public transportation Podgorica, bus schedules, bus stops, fares, city transportation, Podgorica travel, bus network, transport guide, public transit, Podgorica transportation system"
        ></meta>
        <link rel="canonical" href="/"></link>
      </Helmet>
      <div className="description" style={isTextHidden ? {visibility: "hidden", zIndex: -1} : undefined}>
        <h1>Welcome to Podgorica Bus Routes</h1>
        <p>
          Plan your journey and explore the city's extensive bus network to
          reach your desired destinations conveniently
        </p>
      </div>
      <select
        title="Choose line"
        className="select"
        onChange={(e) => {
          setSelectedLine(parseInt(e.currentTarget.value));
        }}
        value={selectedLine ?? 0}
      >
        {!selectedLine && (
          <option value={0} disabled>
            Choose line
          </option>
        )}
        {options.map((x) => (
          <option key={x.value + x.label} value={x.value}>
            {x.label}
          </option>
        ))}
      </select>
      <MapContainer center={[42.4412, 19.2622]} zoom={14}>
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {lineCurves}
      </MapContainer>
    </>
  );
}

export default App;
